$brd-10 : 10px;
$brd-30 : 30px;
$p10: 10px;
$zero: 0 !important;
$white: white;
$black: black;
$blk0: #ffffff00;
$blk40: rgba(26, 47, 62, 0.486);
$prtge100: 100%;
$flex: flex;
$row-r: row-reverse;
$brd1: 1px;
$sld: solid;
 
/**edicion search*/
.busqueda form#search-block-form {
    display: $flex;
    flex-direction: $row-r;
}

.busqueda form#search-block-form {
    display: $flex;
    flex-direction: $row-r;
    border: $brd1 $sld $black;
    border-radius: $brd-10;
    padding: 4% 1%;
}


.busqueda input#edit-keys--2, .busqueda input#edit-keys {
    border: none;
    background: $blk0;
    border-left-width: $brd1;
    border-left-color: $black;
    border-left-style: $sld;
    padding-left: $p10;
}
.busqueda input#edit-keys--2:focus-visible, .busqueda input#edit-keys:focus-visible {
    outline: none;
}
.busqueda input#edit-submit, .busqueda input#edit-submit--3 {
    background: $blk0;
    border: none;
}


/**edicion area de titulo de cada pagina**/
.back-title{
    background-image: url('../images/computer-g4b10d37c4_1920.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y:center;
    height: 30vh;
    border-radius: $brd-30 ;
    color: $white;
    padding: $zero;
    margin: $zero;
}
.blur{
    background-color: $blk40;
    width: $prtge100;
    height: $prtge100;
    border-radius: $brd-30;
}
.idiomas ul.links {
    background: black;
}
.custom-flex div {
    display: flex;
}